.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

html, body {
  height: 100%;
}

.app {
  height: inherit;
}

.app-main {
  padding-top: 50px;
  transform: translate3d(0, 0, 0);
  height: inherit;
}

.app-nav {
  position: fixed;
  min-width: 100%;
  z-index: 1024;
  box-shadow: 0 2px 3px rgba(17, 17, 17, 0.1), 0 0 0 1px rgba(17, 17, 17, 0.1);
}

.nprogress-container {
  position: fixed !important;
  width: 100%;
  height: 50px;
  z-index: 2048;
  pointer-events: none;
}

.nprogress-container #nprogress .bar {
  background: #23d160;
}

.nprogress-container #nprogress .peg {
  box-shadow: 0 0 10px #23d160, 0 0 5px #23d160;
}

.nprogress-container #nprogress .spinner-icon {
  border-top-color: #23d160;
  border-left-color: #23d160;
}
